export interface Component {
  id: number
  name?: string
  root: boolean
  className: string
  tagName: 'div'
  type: NodeType // 1 div; 2 
  style: any
  activeStyle?: any
  event: any,
  eventCallbackHandler: (e: Event) => void,
  text: string
}
export type NodeType = 1 | 2
export type ComponentStyle = {
  base: any
  margin: any
  padding: any
  font: any
  background: any
  border: any
  boxShadow: any
}
